import { makeStyles } from '@material-ui/styles'

export default makeStyles(theme => ({
    projectCard: {
        border: "1px solid #bac5ff !important",
        height: "100%"
    },
    cardContent: {
        height: "100px",
        color: "#000"
        
    },
    cardHeader: {
        backgroundColor: theme.palette.primary.main,
        '& span': {
            fontWeight: "bold",
            color: "#FFF"
        }
    }
}))