import React from 'react'
import { Grid, Box, Paper, Card, CardMedia, Typography, Divider, Chip } from '@material-ui/core'

import useStyles from './styles'
import myPhoto from '../../assets/my-photo.jpg'

import Rating from '@material-ui/lab/Rating';
import GitIcon from '@material-ui/icons/GitHub'
import Instagram from '@material-ui/icons/Instagram'
import LinkedIn from '@material-ui/icons/LinkedIn'
import ProjectCard from '../ProjectCard'

export default function () {
    const classes = useStyles()

    return (
        <>
            <Grid container spacing={2}>
                {/* my photo */}
                <Grid item xs={12} sm={6} >
                    <Box marginTop={3} marginX={1} height={1} display="flex" alignItems="center" justifyContent="center">
                        <Box padding={1} boxShadow={5} bgcolor="secondary.light" >
                            <Card>
                                <CardMedia
                                    component="img"
                                    alt="Alfredo Tito"
                                    className={classes.media}
                                    image={myPhoto}
                                    title="Alfredo Tito"
                                />
                            </Card>
                        </Box>
                    </Box>
                </Grid>
                {/* about me */}
                <Grid item xs={12} sm={6} >
                    <Box marginTop={3} marginX={1} display="flex" flexDirection="column" justifyContent="space-around" alignItems="center" >
                        <Grid container spacing={4}>
                            {/* Bio */}
                            <Grid item>
                                <Paper elevation={3} className={classes.boxShadow}>
                                    <Box textAlign="center" width={1} padding={1} 
                                        bgcolor="primary.main"
                                        boxShadow={4} color="#FFF">
                                        <Typography gutterBottom variant="h5" component="h2">
                                            <b>Sobre mim</b>
                                        </Typography>
                                    </Box>
                                    <Box padding={1}>
                                        <Typography variant="body1">
                                            Eu sou Alfredo, sou Web Designer. Eu moro em São Luís, do Maranhão, Brasil. Fui pesquisador no laboratório de pesquisa telemídia, onde eu desenvolvi o projeto Fábulas para criação de histórias interativas. Atualmente realizo estágio na UNA-SUS desenvolvendo recursos educacionais.
                                            <br/>
                                            <Divider/>
                                            Social Media:
                                            <br/>
                                            <Box width={0.25} display="flex" justifyContent="space-between">
                                                <a href="https://github.com/alfredots">
                                                    <GitIcon/>
                                                </a>
                                                <a href="https://www.instagram.com/garotocaos_/">
                                                    <Instagram/>
                                                </a> 
                                                <a href="https://www.linkedin.com/in/alfredo-tito-837429ba/">
                                                    <LinkedIn/>
                                                </a> 
                                            </Box> 
                                        </Typography>
                                    </Box>
                                </Paper>
                            </Grid>
                            {/* Skills */}
                            <Grid item className={classes.grid}>
                                <Paper elevation={3} className={classes.boxShadow}>
                                    <Box textAlign="center" width={1} padding={1} 
                                        bgcolor="primary.main"
                                        boxShadow={4} color="#FFF">
                                        <Typography gutterBottom variant="h5" component="h2">
                                            <b>Habilidades</b>
                                        </Typography>
                                    </Box>
                                    <Box className={classes.chipBox}>
                                        <Chip color="primary" label="Desenvolvimento Web" />
                                        <Chip color="primary" label="Javascript" />
                                        <Chip color="primary" label="CSS" />
                                        <Chip color="primary" label="Stylus" />
                                        <Chip color="primary" label="React" />
                                        <Chip color="primary" label="MongoDB" />
                                    </Box>
                                </Paper>    
                            </Grid>
                        </Grid>
                    </Box>
                </Grid>
                {/* Projects */}
                <Grid item xs={12}>
                    <Box width={1} height={"400px"} padding={1} borderRadius={100} >
                        <Paper style={{width:"100%", height:"100%"}}>
                            <Box textAlign="center" width={1} padding={1} 
                                bgcolor="primary.main"
                                boxShadow={4} color="#FFF">
                                <Typography gutterBottom variant="h5" component="h2">
                                    <b>Projects</b>
                                </Typography>
                            </Box>
                            <Box width={1} height={1}>
                                <Grid container style={{height:"85%", overflow:"auto"}}>
                                    <Grid item xs={12} sm={6}>
                                        <ProjectCard
                                            projectName="Krilow"
                                            description="Um álbum de fotos interativos que utiliza as fotos do instagram para preenche-los."
                                            link="https://alfredots.github.io/projects/krilow/#/"
                                        />
                                        <ProjectCard
                                            projectName="CanvasJS"
                                            description="Paint desenvolvido utilizando canvas do HTML5."
                                            link="https://alfredots.github.io/canvas-js/"
                                        />
                                        <ProjectCard
                                            projectName="Bowling Unity"
                                            description="Jogo de boliche desenvolvida na cadeira de jogos digitais."
                                            link="https://alfredots.github.io/BowlingUnity/"
                                        />
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <ProjectCard
                                            projectName="Calculator React"
                                            description="Calculadora desenvolvida de React"
                                            link="https://alfredots.github.io/projects/react-calculator/"
                                        />
                                        <ProjectCard
                                            projectName="Book Prototype"
                                            description="Protótipo de livro interativo"
                                            link="https://book-prototype.herokuapp.com/"
                                        />
                                        <ProjectCard
                                            projectName="Sonyar"
                                            description="Jogo desenvolvido na Game Jam onde uma vampira precisa atravessar sua carverna utilizando o sonar."
                                            link="https://alfredots.github.io/Sonyar/"
                                        />
                                    </Grid>
                                </Grid>
                            </Box>
                        </Paper>
                    </Box>
                </Grid>
            </Grid>
            
        </>
    )
}
